export function getAllSubjectList (params = {}) {
  return ajax.fetchGet(path + `/finance/capitalLedger/getAllFinalStageList`, params)
}
// 新增资金账户
export function addCapital (params = {}) {
  return ajax.fetchPostForm(path + `/finance/capital-account/save`, params)
}
// 获取单个资金账户
export function getCapitalId (params = {}) {
  return ajax.fetchGet(path + `/finance/capital-account/getById`, params)
}
