const user = {
  state: {
    count: 0
  },
  mutations: {
    SET_PARAMS: (state, param) => {
      state.count++
    }
  },
  actions: {
    setParams ({commit}, param) {
      commit('SET_PARAMS', param)
    }
  }
}
export default user


