<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <el-row>
  <el-button>默认按钮</el-button>
  <el-button type="primary" @click="path">主要按钮</el-button>
  <el-button type="success">成功按钮</el-button>
  <el-button type="info">信息按钮</el-button>
  <el-button type="warning">警告按钮</el-button>
  <el-button type="danger">危险按钮</el-button>
</el-row>
  </div>
</template>

<script>
import {getCapitalList} from '@/api/capital.js'
import {mapActions} from 'vuex'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    this.setParams()
    console.log(this.$store.getters.count)
    this._getList()
  },
  methods: {
    ...mapActions(['setParams']),
    _getList () {
      let params = {
          accountId: '1115259213954834433',
          capitalAccountType: '',
          capitalAccountName: '',
          pageNum: 1,
          pageSize: 10
      }
      getCapitalList(params).then(res => {
           if (res.code === 0) {
             console.log(res)
           } else {
             this.$message.error(res.desc)
           }
      })
    },
    path () {
      this.$router.push({path: 'model'});
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



