/* jshint esversion: 6 */
import axios from 'axios'
import storage from 'good-storage'
import router from '@/router/index.js'
import store from '@/store/index'
import { Message } from 'element-ui'
let qs = require('qs')
const root = process.env.API_ROOT
axios.defaults.timeout = 10000
axios.defaults.baseURL = root
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
axios.defaults.withCredentials = true  // 跨域
axios.interceptors.request.use( // 利用axios拦截器鉴权和写公共的错误分支
  config => {
    let obj = window.localStorage.getItem('token')
    let token = ''
    if (obj) {
      token = JSON.parse(obj).content
    }
    if (token) {
      axios.defaults.baseURL = root
      config.headers.Authorization = token
    } else {
      if (location.href.indexOf('?') === -1) {
        // 跳转登录页面
          // router.push('/login')
      }
    }
    return config
  },
  error => {
    console.log(error)
  }
)

// respone拦截器
axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    // 超时
    if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
      Message.error('请求超时')
      return Promise.reject(error)
    }
    // 错误提示
    if (error && error.response) {
        switch (error.response.status) {
        case 400:
          alert('请求错误')
          break
        case 403:
          Message.error('拒绝访问')
          break
        case 404:
          Message.error(`请求地址出错: ${error.response.config.url}`)
          break
        case 405:
          Message.error('方法不被允许')
          break
        case 408:
          Message.error('请求超时')
          break
        case 415:
          Message.error('415错误')
          break
        case 500:
          Message.error('服务器内部错误')
          break
        case 501:
          Message.error('服务未实现')
          break
        case 502:
          Message.error('网关错误')
          break
        case 503:
          Message.error('服务不可用')
          break
        case 504:
          Message.error('网关超时')
          break
        case 505:
          Message.error('HTTP版本不受支持')
          break
        default:
      }
      return Promise.reject(error)
    }
    if (error.response && error.response.status === 401) {
      if (error.response.data.code === 10004) {
        return doRequest(error)
      } else if (error.response.data.code === 10001) {
        Message.error('token过期,请重新登录')
        localStorage.clear()
        router.push('/login')
      } else if (error.response.data.code === 10010) {
        localStorage.clear()
        router.push('/login')
      } else {
        Message.error('未知错误,请重新登录')
        router.push('/login')
      }
    } else {
      return Promise.reject(error)
    }
  }
)

async function doRequest (error) {
  const res = error.response.data
  let user = storage.get('user')
  user.token = res.Authorization // store.dispatch('SetToken', res.Authorization)
  store.dispatch('SetToken', res.Authorization)
  let configSelf = error.response.config
  configSelf.baseURL = ''
  configSelf.headers.Authorization = res.Authorization
  const resa = await axios(configSelf)
  return resa
}

export default {
  // blob对象get
  fetchGet1 (url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.get(url, {
        responseType: 'blob',
        params,
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'indices' })
        }}).then(res => {
        resolve(res)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchGet (url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.get(url, {
        params,
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'indices' })
        }}).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // fetchDelete (url, params = {}) {
  //   return new Promise((resolve, reject) => {
  //     axios.delete(url, {params}).then(res => {
  //       resolve(res.data)
  //     }).catch(error => {
  //       reject(error)
  //     })
  //   })
  // },
  fetchPut (url, params = {}, type = '') {
    return new Promise((resolve, reject) => {
      axios.put(url, qs.stringify(params, type)).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchQuickSearch (url) {
    return new Promise((resolve, reject) => {
      axios.get(url).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchPost (url, params = {}, type = '') {
    return new Promise((resolve, reject) => {
      axios.post(url, qs.stringify(params, type)).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchPostForm (url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.post(url, qs.stringify(params, {arrayFormat: 'indices', allowDots: true})).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchPostFile (url, formData) {
    let headers = {headers: {'Content-Type': 'multipart/form-data'}}
    return new Promise((resolve, reject) => {
      axios.post(url, formData, headers).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchAll (params) {
    return new Promise((resolve, reject) => {
      axios.all([...params]).then(axios.spread((...results) => {
        resolve(results)
      })).catch(error => {
        reject(error)
      })
    })
  },
  fetchDelete (url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.delete(url, {params}).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  fetchDeleteForm (url, params) {
    return new Promise((resolve, reject) => {
      axios.delete(url, {data: qs.stringify(params)}).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  }
}



